import React from "react";
import { Route } from 'react-router-dom';
import gsap from "gsap";

//component
import Header from "./components/header";
import Navigation from "./components/navigation";

//styles
import "./styles/App.scss";

//pages
import Home from "./pages/home";
import CaseSudies from './pages/caseStudies';
import Approach from './pages/approach';
import Services from './pages/services';
import About from './pages/about';

//routes
const routes = [
  { path: '/', name: 'Home', Component: Home },
  { path: '/case-studies', name: 'Case Studies', Component: CaseSudies },
  { path: '/approach', name: 'Approach', Component: Approach },
  { path: '/services', name: 'Services', Component: Services },
  { path: '/about-me', name: 'About me', Component: About },
]

function debounce(fn, ms) {
  let timer;
  return () => {
    clearTimeout(timer);
    timer = setTimeout(() => {
      timer = null;
      fn.apply(this, arguments);
    }, ms);
  }
}

function App() {

  //prevent flash
  gsap.to("body", { duration: 0, css: { visibility: "visible" } });

  const [dimensions, setDimensions] = React.useState({
    height: window.innerHeight,
    width: window.innerWidth

  })

  React.useEffect(() => {

    let vh = dimensions.height * .01;
    document.documentElement.style.setProperty('--vh', `${vh}px`)

    /*const HandleResize = () => {
      setDimensions({
        height: window.innerHeight,
        width: window.innerWidth
      });
    };*/

    const debouncedHandlerResize = debounce(function handleResize() {
      setDimensions({
        height: window.innerHeight,
        width: window.innerWidth
      });
    }, 1000);

    window.addEventListener('resize', debouncedHandlerResize);

    return () => {
      window.removeEventListener('resize', debouncedHandlerResize);
    }

  }, [])

  return (

    <>
      <Header dimensions={dimensions} />
      {console.log(dimensions)}
      <div className='App'>
        {routes.map(({ path, Component }) => (
          <Route key={path} exact path={path}>
            <Component />
          </Route>
        ))}
      </div>
      <Navigation />
    </>
  );
}

export default App;
