import gsap from "gsap";

let timeline = gsap.timeline();

export const homeAnimation = (completeAnimation) => {
    timeline.from('.line span', 1, {
        y: 100,
        ease: "power4.out",
        delay: 0.5,
        skewY: 6,
        stagger: {
            amount: 1.2
        }
    }).to('.overlay-top', 1.6, {
        width: 0,
        ease: 'expo.inOut',
        stagger: 0.4
    }).to('.overlay-footer', 1.6, {
        width: 0,
        ease: 'expo.inOut',
        delay: -1.6
    }).to('.overlay-bottom', 1.6, {
        height: 0,
        ease: 'expo.inOut',
        delay: -1.0,
        stagger: {
            amount: -0.6
        }
    }).to('.intro-overlay', { duration: 0, css: { display: "none" } })
        .from('.case-image img', 1.6, {
            scale: 1.4,
            ease: 'expo.inOut',
            delay: -2,
            stagger: {
                amount: -0.4
            },
            onComplete: completeAnimation
        });
}